
 .skills {
  text-align: left;

  h4 {
    text-align: left;
    font-weight: lighter;
  }
  span {
    font-size: 18px;
    font-weight: 800;
  }
}

.about {
  display: flex;

  .content {
    text-align: left;
    padding: 20px;

    h1 {
      font-size: 48px;
      font-weight: bolder;
    }

    p {
      font-size: 24px;
      margin-left: 16px;
    }
  }

  .profile {
    width: 40%;
    min-width: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    h2 {
      text-align: center;
    }
  }



 

  .about-image-cropper {
      // position: relative;
      overflow: hidden;
      width: 50%;
      min-width: 250px;
      height: 250px;
      min-height: 250px;
      border-radius: 50%;
      background-color: $white;
      border: 2px solid $white;
      margin: 20px;
      // margin-top: 48px;

    .about-image{
      position: relative;
      width: 100%;
      height: 250px;
      // margin-top: 5px;
    }
  }
}